/* eslint-disable jsx-a11y/tabindex-no-positive */
import { useState, useEffect, useRef } from 'react';
import { useSelector } from 'react-redux';
import { useNavigate } from 'react-router-dom';

// material-ui
import { Typography, List, ListItem, useMediaQuery } from '@mui/material';
import { useTheme } from '@mui/material/styles';

// project imports
import MainCard from 'ui-component/cards/MainCard';
import { Languages } from './Languages';
import EditStep from './component/EditStep';
import ProductCategory from './component/ProductCategory';
import PhotoGallery from './component/PhotoGallery';
import ProductAttribute from './component/ProductAttribute';
import ProductDetail from './component/ProductDetail';
import Confirmation from './component/Confirmation';

// ==============================|| SINGLE PAGE APPLICATION ||============================== //
let productModified = {};

const App = () => {
    //全局数据
    console.log('productEdit app running');
    let localization = useSelector((state) => state.localization); //多语言相关
    let globalization = useSelector((state) => state.globalization); //全局数据
    let [langPack, setLangPack] = useState(Languages[localization.locale]); //currentLanguage
    let [loginData] = useState(globalization.baseData.loginData);
    let navigate = useNavigate(); //react-router-dom
    //编辑步骤提示 & 菜单折叠
    let [activeStep, setActiveStep] = useState(0);
    let [expanded, setExpanded] = useState({ panel1: true, panel2: false, panel3: false, panel4: false });
    //产品信息汇总
    let product_id = location.pathname.replace('/product/', ''); //产品id
    let [productInfo, setProductInfo] = useState({}); //产品信息
    let [finalAttributeArr, setFinalAttributeArr] = useState([]); //在用的属性列表
    let [finalImageArr, setFinalImageArr] = useState([]); //在用的图片列表
    let [finalDescription, setFinalDescription] = useState(''); //在用的产品详情
    let [finalCatId, setFinalCatId] = useState(0); //当前产品类目id
    let [categories, setCategories] = useState([]); //当前产品类目
    let categoryArr = []; //当前产品类目(暂存)
    let menuRef = useRef(null);
    let [menuState, setMenuState] = useState({
        menuLevel: undefined,
        menuValue: undefined,
        menuArr: [],
        anchorEl: null
    });
    //产品主图相关
    let photoCols = useMediaQuery(useTheme().breakpoints.down('md')) ? 3 : 6;
    let uploaderRef = useRef(null); //上传表单
    //产品属性相关
    let [allAttributeArr, setAllAttributeArr] = useState([]); //当前类目下的所有可用属性列表

    useEffect(() => {
        if (!categories.length) return;
        let categoryResult = categories[categories.length - 1];
        let cat_id = categoryResult.category_id;
        setFinalCatId(cat_id);
        if (cat_id) getCategoryAttributeInfo({ cat_id });
        //console.log(categories, categoryResult.category_id);
        return; //类目调整 返回最新的类目id
    }, [categories]);

    useEffect(() => {
        console.log('description changed ');
        if (finalDescription) {
            if (!productInfo.description) return; //待初始化
            if (finalDescription.length > 60000) return queryResult(false, langPack.form.descriptionOverflow);
            if (finalDescription === productInfo.description) return; //未修改
            productModified.description = finalDescription.replace(/\n/g, '').trim();
        }
        return; //编辑器内容改变时
    }, [finalDescription]);

    useEffect(() => {
        setLangPack(Languages[localization.locale]);
        return; //语言切换
    }, [localization.locale]);

    useEffect(() => {
        console.log('I want product editing');
        if (!loginData) return navigate('/');
        getProductInfo({ product_id });
        return () => {
            console.log('I leave product edit');
            productModified = {};
        }; //载入 & 清理
    }, []);

    //____________一些函数_______________________________________________
    async function uploadPhotos(photoFile) {
        let currentFolder = -1;
        let paramObj = ma.photoUploadQuery({ file_name: photoFile.name, image_bytes: photoFile, group_id: currentFolder });
        let msg = await fetchAPI(paramObj);
        if (!msg.response.status) return queryResult(false, langPack.common.yes);
        let res = ma.photoUploadCheck(msg);
        let { status, result } = res.response;
        if (!status) return queryResult(false, result);
        queryResult(true, langPack.steps.uploadSuccess);
        return setFinalImageArr((prev) => {
            if (prev.length >= 6) return prev;
            let newArr = [result.photobank_url, ...prev];
            productModified.main_image = { images: newArr.toString() };
            return newArr;
        });
    }

    async function updateProductInfo(obj) {
        let paramObj = ma.productFieldUpdateQuery(obj);
        let msg = await fetchAPI(paramObj);
        if (!msg.response.status) return queryResult(false, langPack.common.netFail + '\n' + msg.response.result);
        let res = ma.productFieldUpdateCheck(msg);
        let { status, result } = res.response;
        queryResult(status, result);
        console.log(result);
        return true;
    } //更新产品信息

    async function getProductInfo(obj) {
        let paramObj = ma.productDetailQuery(obj);
        let msg = await fetchAPI(paramObj);
        if (!msg.response.status) return queryResult(false, langPack.common.netFail + '\n' + msg.response.result);
        let res = ma.productDetailCheck(msg);
        let { status, result } = res.response;
        if (!status) return queryResult(status, result);
        console.log(result);
        if (result.attributes.product_attribute) {
            let attributeArr = [...result.attributes.product_attribute];
            attributeArr.sort((a, b) => a.attribute_id - b.attribute_id);
            setFinalAttributeArr(attributeArr);
        }
        if (result.main_image.images.string) {
            setFinalImageArr(result.main_image.images.string);
        }
        if (result.description) {
            setFinalDescription(result.description);
        }
        setProductInfo(result);
        if (result.category_id) getAllCategories(result.category_id);
    } //获取产品信息

    async function getCategoryInfo(obj) {
        let paramObj = ma.productCategoryQuery(obj);
        let msg = await fetchAPI(paramObj);
        if (!msg.response.status) return queryResult(false, langPack.common.netFail + '\n' + msg.response.result);
        let res = ma.productCategoryCheck(msg);
        let { status, result } = res.response;
        if (!status) return queryResult(status, result);
        return result;
    } //获取类目信息

    async function getCategoryAttributeInfo(obj) {
        let paramObj = ma.productCategoryAttributeQuery(obj);
        let msg = await fetchAPI(paramObj);
        if (!msg.response.status) return queryResult(false, langPack.common.netFail + '\n' + msg.response.result);
        let res = ma.productCategoryAttributeCheck(msg);
        let { status, result } = res.response;
        if (!status) return queryResult(status, result);
        console.log(result);
        return setAllAttributeArr(result);
    } //获取类目属性信息

    async function getAllCategories(category_id, category_level) {
        if (!category_level) {
            let categoryResult = await getCategoryInfo({ cat_id: category_id });
            if (!categoryResult) return;
            let { level, parent_ids } = categoryResult;
            if (!categoryArr.length) categoryArr = new Array(level); //初始化模式
            categoryArr[level] = categoryResult;
            if (level > 0 && parent_ids && parent_ids.number) {
                let cat_id = parent_ids.number[0];
                getAllCategories(cat_id);
            } else {
                setCategories(categoryArr);
            }
            return;
        }
        let categoryResult = categories[category_level - 1].child_ids.number.find((obj) => obj.category_id === category_id);
        if (!categoryResult) return;
        let { child_ids } = categoryResult;
        let newCategories = [...categories];
        newCategories[category_level] = categoryResult;
        if (category_level + 1 < newCategories.length) newCategories.length = category_level + 1;
        if (child_ids) {
            newCategories.push({ category_id: 0 });
            categoryArr = newCategories;
            showMenu(newCategories.length - 1);
        }
        return setCategories(newCategories); //修改类目模式
    }

    const showMenu = async (index) => {
        let latestCategories = categoryArr.length ? categoryArr : categories;
        let category_id = latestCategories[index].category_id;
        let childIdsArr = latestCategories[index - 1].child_ids.number;
        for (let cat_id of childIdsArr) {
            if (typeof cat_id === 'object') break;
            let categoryResult = await getCategoryInfo({ cat_id });
            childIdsArr[childIdsArr.indexOf(cat_id)] = categoryResult;
        }
        setMenuState({ menuLevel: index, menuValue: category_id, menuArr: childIdsArr, anchorEl: menuRef.current });
    }; //获取当前的类目id & 通过父级获取子类目选项
    const closeMenu = (category_id) => {
        let { menuLevel } = menuState;
        setMenuState({ menuArr: [], anchorEl: null });
        console.log(category_id);
        if (category_id !== undefined && category_id !== finalCatId) getAllCategories(category_id, menuLevel);
    }; //需要更新类目

    const onPhotoSelected = async (e) => {
        let files = e.target.files;
        if (!files.length) return;
        for (let file of files) {
            await uploadPhotos(file);
        }
        e.target.files = null;
    };

    const onDoubleClickImageBtn = (url) => {
        if (finalImageArr.length <= 1) return queryResult(false, langPack.steps.mainPhotoRequired);
        let newArr = finalImageArr.filter((imageUrl) => imageUrl !== url);
        productModified.main_image = { images: newArr.toString() };
        return setFinalImageArr(newArr);
    };

    const onAttributeClear = () => {
        let attributeArr = [];
        if (!finalAttributeArr.length) return;
        attributeArr = [...finalAttributeArr];
        attributeArr = attributeArr.filter((obj) => obj.value_name);
        productModified.attributes = attributeArr;
        return setFinalAttributeArr(attributeArr);
    }; //最后确认

    const onAttributeAdd = (attributeSelected) => {
        console.log(attributeSelected);
        let { attr_id, attr_value, en_name, input_type } = attributeSelected;
        if (!attr_id) return console.log('缺少关键id');
        let attributeArr = [];
        if (finalAttributeArr.length) {
            attributeArr = [...finalAttributeArr];
            if (finalAttributeArr.find((obj) => obj.attribute_id === attr_id)) {
                if (/multi/gi.test(input_type)) {
                    if (attr_value && attr_value.attr_value_id) {
                        attributeArr = attributeArr.filter((obj) => obj.value_id !== attr_value.attr_value_id);
                    } //多选模式下排除重复值👆 单选的话排除重复属性👇
                } else {
                    attributeArr = attributeArr.filter((obj) => obj.attribute_id !== attr_id);
                    queryResult(false, langPack.form.attributeDuplicated);
                }
            } //如果有重复的，替换并提醒用户
        }
        let attributeObj = {
            attribute_id: attr_id,
            attribute_name: en_name,
            value_name: ''
        };
        if (attr_value) {
            attributeObj.value_id = attr_value.attr_value_id;
            attributeObj.value_name = attr_value.en_name;
        }
        let newArr = [attributeObj, ...attributeArr];
        productModified.attributes = newArr;
        setFinalAttributeArr(newArr);
        console.log(attributeObj);
    }; //添加新属性

    const arrayDataReplacer = (newData, oldArr, searchKey) => {
        let newArr = [...oldArr];
        if (newArr.length) {
            let oldIndex = newArr.findIndex((obj) => obj[searchKey] === newData[searchKey]);
            //查找是否有旧的数据，找到匹配项则替换，没有则添加
            if (oldIndex !== -1) newArr[oldIndex] = newData;
            else newArr.push(newData);
        } else newArr.push(newData);
        return newArr;
    };
    const objectCheck = (obj) => {
        return typeof obj === 'object' && obj !== null && !Array.isArray(obj);
    }; //检查是否为[Object object]

    const onAttributeModified = (key, value) => {
        if (!value) return queryResult(false, langPack.steps.modificationNull);
        //部分数据比较复杂，单独处理
        if (/attributes|bulk_discount_prices/g.test(key)) {
            let oldArr;
            let searchKey;
            if (key === 'attributes') {
                searchKey = 'attribute_id';
                oldArr = finalAttributeArr;
                let newArr = arrayDataReplacer(value, oldArr, searchKey);
                productModified.attributes = newArr;
                return setFinalAttributeArr(newArr);
            } else if (key === 'bulk_discount_prices') {
                searchKey = 'start_quantity';
                //oldArr = [];
                return console.log('待部署'); //待部署
            } //数组类型，需要查找/替换，可考虑提前return，如有必要
        } else if (key === 'keywords') {
            value = value.split(',');
            value = value.map((str) => str.trim());
            if (value.length > 3) value.length = 3;
            productModified.keywords = String(value);
            return productModified;
        } else if (key === 'subject') {
            if (value.length > 128) return queryResult(false, langPack.form.subject);
            productModified.subject = value;
            return productModified;
        } //先初始化数据容器，如果之前没有数据的话
        if (productModified[key] === undefined) {
            productModified[key] = JSON.parse(JSON.stringify(productInfo[key]));
            if (key === 'sourcing_trade') {
                if (productModified[key]['payment_methods']) {
                    let payment_methods = productModified[key]['payment_methods']['string'];
                    productModified[key]['payment_methods'] = payment_methods;
                }
                if (productModified[key]['deliver_periods']) {
                    let deliver_periods = productModified[key]['deliver_periods']['deliver_period'];
                    productModified[key]['deliver_periods'] = deliver_periods;
                }
            }
        }
        productModified[key] = objectCheck(productModified[key]) ? { ...productModified[key], ...value } : value;
        return productModified;
    }; //转换成最终的key/value, 如果是数组，则需要查找&替换
    const modificationSubmission = () => {
        let modifiedKeys = Object.keys(productModified);
        if (!modifiedKeys.length) return queryResult(false, langPack.steps.modificationNull);
        let dataObj = {
            category_id: finalCatId,
            product_id: productInfo.product_id,
            product_type: productInfo.product_type
        }; //默认提交的数据
        for (let [k, v] of Object.entries(productModified)) {
            dataObj[k] = typeof v == 'object' ? JSON.stringify(v) : v;
        } //带上修改的数据
        console.log(dataObj);
        updateProductInfo(dataObj);
    };

    //____________Functions return JSX_______________________________________________
    function ModificationConfirmation() {
        let modifiedKeys = Object.keys(productModified);
        if (!modifiedKeys.length) return <Typography variant="h2">{langPack.steps.modificationNull}</Typography>;
        return (
            <>
                <Typography variant="h2">{langPack.steps.modificationDone}</Typography>
                <List>
                    {modifiedKeys.map((key) => (
                        <ListItem key={key}>-- {key}</ListItem>
                    ))}
                </List>
            </>
        );
    }

    return (
        <MainCard title="Product Edit --产品编辑">
            <EditStep langPack={langPack} activeStep={activeStep} setActiveStep={setActiveStep} />
            {activeStep === 0 ? (
                <>
                    <ProductCategory
                        localization={localization}
                        categories={categories}
                        menuRef={menuRef}
                        menuState={menuState}
                        showMenu={showMenu}
                        closeMenu={closeMenu}
                    />
                    <PhotoGallery
                        localization={localization}
                        finalImageArr={finalImageArr}
                        imageSizing={{ width: 30, height: 30 }}
                        photoCols={photoCols}
                        rowHeight={200}
                        uploaderRef={uploaderRef}
                        onDoubleClickImageBtn={onDoubleClickImageBtn}
                        onPhotoSelected={onPhotoSelected}
                        langPack={langPack}
                    />
                    <ProductAttribute
                        {...productInfo}
                        langPack={langPack}
                        expanded={expanded}
                        setExpanded={setExpanded}
                        finalAttributeArr={finalAttributeArr}
                        allAttributeArr={allAttributeArr}
                        onAttributeAdd={onAttributeAdd}
                        onAttributeClear={onAttributeClear}
                        onAttributeModified={onAttributeModified}
                    />
                </>
            ) : activeStep === 1 ? (
                <>
                    <ProductDetail
                        localization={localization}
                        finalDescription={finalDescription}
                        setFinalDescription={setFinalDescription}
                        langPack={langPack}
                    />
                </>
            ) : activeStep === 2 ? (
                <>
                    <ModificationConfirmation />
                    <Confirmation langPack={langPack} modificationSubmission={modificationSubmission} />
                </>
            ) : (
                <></>
            )}
        </MainCard>
    );
};

export default App;
