import PropTypes from 'prop-types';

// material-ui
import { Button, FormControlLabel, ImageList, ImageListItem, ImageListItemBar, Input, IconButton, Tooltip } from '@mui/material';
import { StarBorder as StarBorderIcon, FileUpload as UploadIcon, AddPhotoAlternate as AddPhotoAlternateIcon } from '@mui/icons-material';

function PhotoGallery({ finalImageArr, imageSizing, photoCols, rowHeight, uploaderRef, onDoubleClickImageBtn, onPhotoSelected, langPack }) {
    //console.log('loading photo gallery...');
    let { width, height } = imageSizing;
    return (
        <ImageList cols={photoCols} rowHeight={rowHeight} sx={{ marginTop: '10px', marginBottom: '10px' }} variant="quilted" gap={2}>
            {finalImageArr.map((url, index) => {
                return (
                    <ImageListItem key={index} cols={1} rows={1}>
                        <img {...srcset(url, width, height, 1, 1)} alt={index + 1} loading="lazy" />
                        <ImageListItemBar
                            sx={{
                                background: 'linear-gradient(to bottom, rgba(0,0,0,0.7) 0%, ' + 'rgba(0,0,0,0.3) 50%, rgba(0,0,0,0) 100%)'
                            }}
                            title={index + 1}
                            position="top"
                            actionIcon={
                                <Tooltip title={langPack.steps.removePhotos}>
                                    <IconButton
                                        sx={{ color: index ? 'yellow' : 'white' }}
                                        aria-label={`star ${index + 1}`}
                                        onDoubleClick={() => onDoubleClickImageBtn(url)}
                                    >
                                        <StarBorderIcon />
                                    </IconButton>
                                </Tooltip>
                            }
                            actionPosition="left"
                        />
                    </ImageListItem>
                );
            })}
            {finalImageArr.length < 6 ? (
                <>
                    <FormControlLabel
                        sx={{ display: 'none' }}
                        ref={uploaderRef}
                        control={<Input type="file" inputProps={{ multiple: true }} />}
                        label={<AddPhotoAlternateIcon />}
                        onChange={onPhotoSelected}
                    />
                    <Tooltip title={langPack.steps.photoUploadTip}>
                        <Button startIcon={<UploadIcon />} onClick={() => uploaderRef.current.click()} key="uploader"></Button>
                    </Tooltip>
                </>
            ) : null}
        </ImageList>
    );
}

PhotoGallery.propTypes = {
    finalImageArr: PropTypes.array,
    imageSizing: PropTypes.object,
    photoCols: PropTypes.number,
    cols: PropTypes.number,
    rowHeight: PropTypes.number,
    uploaderRef: PropTypes.object,
    onDoubleClickImageBtn: PropTypes.func,
    onPhotoSelected: PropTypes.func,
    onClickImageBtn: PropTypes.func,
    langPack: PropTypes.object
};

export default PhotoGallery;
