import PropTypes from 'prop-types';

// material-ui
import { Menu, MenuItem, Breadcrumbs, Chip } from '@mui/material';
import { Home as HomeIcon, ExpandMore as ExpandMoreIcon } from '@mui/icons-material';
import { emphasize, styled } from '@mui/material/styles';
import { grey } from '@material-ui/core/colors';

//styled components
const BreadChip = styled(Chip)(() => {
    const backgroundColor = grey[100];
    return {
        backgroundColor,
        height: 40,
        '&:hover, &:focus': {
            backgroundColor: emphasize(backgroundColor, 0.06)
        },
        '&:active': {
            boxShadow: 10,
            backgroundColor: emphasize(backgroundColor, 0.12)
        }
    };
});

function ProductCategory({ localization, categories, menuRef, menuState, showMenu, closeMenu }) {
    //console.log('loading product category...');
    let { menuArr, anchorEl } = menuState;
    return (
        <>
            <Breadcrumbs sx={{ display: 'inline-flex', paddingTop: '10px' }}>
                {categories.map((obj, index) => {
                    if (index > 0)
                        return (
                            <BreadChip
                                sx={menuState.menuLevel === index ? { display: 'none' } : {}}
                                component="a"
                                href="#"
                                key={obj.category_id}
                                label={localization.locale !== 'zhCN' ? obj.name : obj.cn_name}
                                icon={index === 1 ? <HomeIcon /> : undefined}
                                deleteIcon={<ExpandMoreIcon />}
                                onDelete={() => showMenu(index)}
                            />
                        );
                })}
            </Breadcrumbs>
            <span ref={menuRef}></span>
            <div>
                <Menu anchorEl={anchorEl} open={Boolean(anchorEl)} onClose={() => closeMenu()}>
                    {menuArr.map((obj) => (
                        <MenuItem key={obj.category_id} onClick={() => closeMenu(obj.category_id)}>
                            {localization.locale !== 'zhCN' ? obj.name : obj.cn_name}
                        </MenuItem>
                    ))}
                </Menu>
            </div>
        </>
    );
}

ProductCategory.propTypes = {
    localization: PropTypes.object,
    finalImageArr: PropTypes.array,
    menuRef: PropTypes.object,
    categories: PropTypes.array,
    menuState: PropTypes.object,
    showMenu: PropTypes.func,
    closeMenu: PropTypes.func
};

export default ProductCategory;
