import { useState } from 'react';
import PropTypes from 'prop-types';

// material-ui
import { Button, Box, Stepper, Step, StepLabel, ButtonGroup } from '@mui/material';

function EditStep({ langPack, activeStep, setActiveStep }) {
    const [steps] = useState([langPack.steps.first, langPack.steps.second, langPack.steps.finally]);
    const handleNext = () => {
        setActiveStep((prevActiveStep) => prevActiveStep + 1);
    };
    const handleBack = () => {
        setActiveStep((prevActiveStep) => prevActiveStep - 1);
    };

    return (
        <>
            <Stepper activeStep={activeStep}>
                {steps.map((label) => {
                    return (
                        <Step key={label}>
                            <StepLabel>{label}</StepLabel>
                        </Step>
                    );
                })}
            </Stepper>
            <ButtonGroup
                variant="contained"
                color="secondary"
                disableElevation
                sx={{ display: 'flex', flexDirection: 'row', pt: 2, paddingBottom: '10px' }}
            >
                <Button disabled={activeStep === 0} onClick={handleBack} sx={{ mr: 1 }}>
                    {langPack.steps.previous}
                </Button>
                <Box sx={{ flex: '1 1 auto' }} />
                <Button disabled={activeStep === 2} onClick={handleNext}>
                    {activeStep === steps.length - 1 ? langPack.steps.complete : langPack.steps.next}
                </Button>
            </ButtonGroup>
        </>
    );
}

EditStep.propTypes = {
    langPack: PropTypes.object,
    activeStep: PropTypes.number,
    setActiveStep: PropTypes.func
};

export default EditStep;
